.App {
  font-family: sans-serif;
}
.month{
  padding: 30px 0 19px;
  margin: 0;
  font-family: Inter;
  font-style: normal;
  font-weight: bolder;
  font-size: 24.3493px;
  line-height: 25px;
  border-radius: 200px;
  background-color: #ffffff;
}

.modal{
  width: 100%;
  margin: auto;
  background-color: #371741;
}
.eachDate{
    min-width: 33px;
    max-width: 33px;
    margin-right: 8px;
    border: 1px solid #ddd;
    border-radius: 15px;
    padding: .7rem .5rem;
    cursor: pointer;
    text-align: center;
    overflow: hidden;
    transition: all .2s ease-in;
}
.img{
  position: relative;
  z-index: -1;
  width: 100%;
}
.img2{
  position: relative;
  width: 80%;
  z-index: 1;
  left: 35px;
  top: 45px;
}

.eachDateSelected{
  min-width: 33px;
  max-width: 33px;
  margin-right: 8px;
  border: 1px solid #ddd;
  border-radius: 15px;
  padding: .7rem .5rem;
  cursor: pointer;
  text-align: center;
  overflow: hidden;
  transition: all .2s ease-in;
  background-color: #ff9169;
  border: 1px solid #ff9169!important;
}
.alldates{
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  margin-bottom: 7px;
  -webkit-tap-highlight-color: transparent;
  -ms-overflow-style: none;
  scrollbar-width: none;
  transition: all 5s ease-in;
}

.alldates::-webkit-scrollbar {
     display: none;
   }

.day{
    transition: color .2s ease-in;
    font-family: "Inter",sans-serif;
    -webkit-font-smoothing: antialiased;
    cursor: pointer;
    text-align: center;
    font-size: .875rem;
    font-weight: 500;
    color: #ccc;
    transition: color .2s ease-in;
}
.dayselected{

    transition: color .2s ease-in;
    font-family: "Inter",sans-serif;
    -webkit-font-smoothing: antialiased;
    cursor: pointer;
    text-align: center;
    font-size: .875rem;
    font-weight: 500;
    color: #ffffff;
    transition: color .2s ease-in;
}
.dt{
    margin: 4px 0;
    color: #3d3e67;
    font-size: 1.2rem;
    font-weight: 700;
    transition: color .2s ease-in;
    line-height: 22px;
    font-family: Inter,sans-serif;
}
.dtselected{
  margin: 4px 0;
  color: #ffffff;
  font-size: 1.2rem;
  font-weight: 700;
  transition: color .2s ease-in;
  line-height: 22px;
  font-family: Inter,sans-serif;
}
.slot-date{
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  color: #000000;
 }
.filled {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  text-align: center;
  color: #000000;
  cursor:pointer;
  width: 90px;
  height: 35px;
  left: 40px;
  top: 642px;
  border: none;
  border-radius: 5px;
  margin: 0 14px 18px 0;
	
}
.selected {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  text-align: center;
  color: #ffffff;
  cursor:pointer;
  width: 90px;
  height: 35px;
  left: 40px;
  top: 642px;
  border: none;
  border-radius: 5px;
  background-color: #ff9169;
  margin: 0 14px 18px 0;
 }

.modal-div{
  background-color: #ffffff;
  border-radius: 30px 30px 0 0;
  padding :24px;
}
 .available {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  text-align: center;
  color: #000000;
  cursor:pointer;
  width: 90px;
  height: 35px;
  left: 40px;
  top: 642px;
  border: solid black 0.8px;
  border-radius: 5px;
  background-color: #ffffff;
  margin: 0 14px 18px 0;
 }
 .confirm {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  text-align: center;
  color: #000000;
  cursor:pointer;
  width: 88px;
  height: 35px;
  left: 40px;
  top: 642px;
  border: solid black 0.5px;
  border-radius: 5px;
  margin: 30px 20px 15px 0;
  background-color: #ffffff;
 }
 .slot{
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 10px;
  color: #6a197d;
 }
 .close{
   position: absolute;
   top : 0;
   left: 0;
 }
 .dot1{
  height: 10px;
  width: 10px;
  background-color: #3d3e67;
  border-radius: 50px;
  color: #3d3e67;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: all .2s ease-in;
  background-color: #ff9169!important;
  margin-right: 8px;
 }
 .dots{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-bottom: 10px;
 }
 .dot2{
  height: 10px;
  width: 10px;
  background-color: #3d3e67;
  border-radius: 50px;
  color: #3d3e67;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: all .2s ease-in;
  margin-right: 5px;
 }
 .allmonths{
  display: inline-block;
  font-size: 1.2rem;
  font-weight: 300;
  cursor: pointer;
  margin-bottom: 15px;

 }
 .currmonth{
  font-family: Inter;
  font-weight: 600;
  font-size: 20.3493px;
  color: #6a197d;
  line-height: 25px;
  
  font-size: 1.2rem;
 }
 .nextmonth{
  font-family: Inter;
  font-style: normal;
  color: #865694;
  font-size: 1.2rem;
  font-weight: 500;
  font-size: 20.3493px;
  line-height: 25px;
  position: absolute;
  right: 10%;
  opacity: 0.7;
 }
